@import url(https://fonts.googleapis.com/css2?family=Maven+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  font-family: "Maven Pro", sans-serif !important;
}

p {
  color: white;
}

.neonFont {
  text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px rgb(238, 255, 0);
}

.neonHeader-effect {
  border: 0.2rem solid #fff;
  border-radius: 2rem;
  padding: 0.4em;
  box-shadow: 0 0 0.2rem #fff, 0 0 0.2rem #fff, 0 0 2rem #bc13fe,
    0 0 0.8rem #bc13fe, 0 0 2.8rem #bc13fe, inset 0 0 1.3rem #bc13fe;
  -webkit-animation: pulsate 2.5s infinite alternate;
          animation: pulsate 2.5s infinite alternate;
}

.neonIcon {
  border-radius: 30%;
  box-shadow: inset 0 0 1em gold, 0 0 1em rgb(217, 255, 0);
}

.header {
  font-size: large;
  font-weight: 700;
  background-color: black;
  /* background: rgb(0, 38, 142);
  background: linear-gradient(
    90deg,
    rgba(0, 38, 142, 1) 0%,
    rgba(108, 0, 100, 1) 100%
  ); */
}

.grad-background {
  background: rgb(0, 38, 142);
  background: linear-gradient(
    90deg,
    rgba(0, 38, 142, 1) 0%,
    rgba(108, 0, 100, 1) 100%
  );
}

.profilepic {
  width: 45%;
  border-radius: 50%;
  box-shadow: inset 0 0 1em gold, 0 0 1em rgb(217, 255, 0);
}

